import React from 'react'
import {useState} from 'react';
import AccordianBody from './AccordianBody';

const Record = (props) => {
    const [isActive, setIsActive] = useState(false);

    return (
        <div className={isActive ? "record-container active" : "record-container"}>
            <div className="record d-flex justify-content-between align-items-center head">
                <div className="d-flex justify-content-start align-items-center parent-div">
                    <div className="initial">
                        {props.record.keyword.charAt(0).toUpperCase()}
                    </div>
                    <div className="docName">
                        <p>{props.record.keyword} Narsary</p>
                    </div>    
                </div> 

                <span onClick={() => setIsActive(!isActive)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#2c3e50" fill="none" strokeLinecap="round" strokeLinejoin="round">
                        <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                        <polyline points="6 9 12 15 18 9" />
                    </svg>
                </span>
            </div>
            {isActive && <AccordianBody fileName={props.record.fileName} pageNo={props.record.pageNo} />}

        </div>
    )
}

export default Record
