import React from 'react';
import {useState,useEffect} from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import Header from './components/Header';
import Results from './components/Results';
import SearchFile from './components/SearchFile';
import Footer from './components/Footer';
// import * as data from './components/db.json';
import AllRecords from './components/AllRecords';
import Loader from './components/Loader';


const App = () =>{
  const [results, setResults] = useState([]);
  const [keyword, setKeyword] = useState('');
  const [records, setRecords] = useState([]);
  const [loader, setLoader] = useState(false);
  useEffect(() => {
      const getRecords = async () => {
        const recordsFromServer = await fetchRecords();
        setRecords(recordsFromServer.data);
        setLoader(true);
      }
  
      getRecords();
    },[])

    // fetch all records
    const fetchRecords = async () => {
      const res = await fetch('https://script.google.com/macros/s/AKfycbzkVVbKMZlbJVIV8gZ2vEjjEbp6G6D5NRbnurWZOnNU7NBmpSFejUheDuthjVfyQnPhgQ/exec');
      const data = await res.json()
      return data;
    }    
  
  
  // search doc
  const findDoc = async (keyword) => {
    setKeyword(keyword);
    const data = records.filter((record) => record.keyword === keyword.toLowerCase() );
    setResults(data);
  }

  const removeResult = (id) => {
    const data = results.filter((result) => result.id !== id );
    setResults(data);
    setKeyword('');
  }

  return (
    <Router>
    <div className="container">
        <Header />
        <Route path='/' exact render={(props) => (
          <>
            <SearchFile onSearch={findDoc} />
            {results.length > 0 ? <Results results={results} onRemove={removeResult} /> : keyword && <p>Sorry, No Record Found for <b>{keyword}</b></p>}  
          </>
        )} />
        <Route path='/allRecords' component={AllRecords} /> 
        <Footer />
        {loader === false && <Loader />}  
    </div>
    </Router>
  )
}

export default App;