import React from 'react'
import {useState,useEffect} from 'react';
import Record from './Record';
import Loader from './Loader';
// import * as data from './db.json';

const AllRecords = () => {
    const [records, setRecords] = useState([]);
    const [loader, setLoader] = useState(false);
    useEffect(() => {
        const getRecords = async () => {
          const recordsFromServer = await fetchRecords();
          setRecords(recordsFromServer.data);
          setLoader(true);
        }
    
        getRecords();
      },[])
    
    
       // fetch all records
    const fetchRecords = async () => {
        const res = await fetch('https://script.google.com/macros/s/AKfycbzkVVbKMZlbJVIV8gZ2vEjjEbp6G6D5NRbnurWZOnNU7NBmpSFejUheDuthjVfyQnPhgQ/exec');
        const data = await res.json();
        return data;
    } 

    return (
        <div className="all-records-container">
            <h3 className="title">All Records</h3>
            { records.length > 0 ? records.map((record) => (<Record key={record.id} record={record} />) ) : <p>No Record Found</p>}
            {loader === false && <Loader />}
        </div>
    )
}

export default AllRecords
